.fanTicketsPoweredBy,
.lOOTBOX {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
}
.lOOTBOX {
  left: 164px;
  font-size: var(--font-size-lg);
  font-family: var(--font-fira-sans);
}
.groupDiv {
  position: relative;
  width: 252px;
  height: 19px;
  flex-shrink: 0;
}
.frameDiv {
  align-self: stretch;
  background-color: var(--color-teal);
  display: flex;
  flex-direction: column;
  padding: var(--padding-3xs) var(--padding-xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.b {
  flex: 1;
  position: relative;
  display: inline-block;
}
.frameDiv1 {
  align-self: stretch;
  background-color: var(--color-gray-500);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xl) var(--padding-4xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-4xl);
  color: var(--color-black);
}
.b1,
.nUMBEROFTEAMS {
  position: relative;
  display: inline-block;
}
.nUMBEROFTEAMS {
  width: 157.91px;
}
.b1 {
  font-size: var(--font-size-3xl);
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: center;
}
.frameButton,
.frameInput {
  border: 0;
  border-radius: var(--br-sm);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.frameButton {
  cursor: pointer;
  padding: 0 var(--padding-sm);
  background-color: var(--color-gray-200);
  display: flex;
  flex-direction: row;
}
.frameInput {
  font-family: var(--font-inter);
  font-size: var(--font-size-2xl);
  background-color: var(--color-gray-100);
  flex: 1;
  padding: var(--padding-4xs) 0;
  width: 100%;
  text-align: center;
}
.frameButton1,
.frameDiv3,
.frameInput {
  display: flex;
  flex-direction: row;
}
.frameButton1 {
  cursor: pointer;
  border: 0;
  padding: 0 var(--padding-2xs);
  background-color: var(--color-gray-200);
  border-radius: var(--br-sm);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.frameDiv3 {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 7px;
}
.cREATEEVENT,
.frameInput1 {
  font-family: var(--font-inter);
}
.frameInput1 {
  border: 0;
  display: flex;
  font-size: var(--font-size-lg);
  background-color: var(--color-gray-100);
  align-self: stretch;
  border-radius: var(--br-sm);
  flex-direction: row;
  padding: var(--padding-xs) 0;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.cREATEEVENT {
  position: relative;
  font-size: var(--font-size-xl);
  display: inline-block;
  color: var(--color-white);
  text-align: center;
}
.frameButton2,
.frameDiv2 {
  align-self: stretch;
  display: flex;
  box-sizing: border-box;
  align-items: center;
}
.frameButton2 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-2xs) var(--padding-md);
  background-color: var(--color-teal);
  border-radius: var(--br-md);
  flex-direction: row;
  justify-content: center;
  margin-bottom: var(--padding-xs);
}
.frameDiv2 {
  flex-direction: column;
  padding: 0 var(--padding-md) 8px;
  justify-content: flex-start;
  gap: var(--gap-md);
  font-size: var(--font-size-lg);
  color: var(--color-gray-400);
}
.xterrangmailcomClickToCh {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-base);
  display: inline-block;
  font-style: italic;
  font-family: var(--font-inter);
  color: var(--color-gray-300);
  text-align: center;
}
.frameDiv4 {
  align-self: stretch;
  background-color: var(--color-gray-500);
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-md) var(--padding-lg);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.startViewAdditionalSetting {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-0);
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-inter);
}
.floatingButtonContainer {
  align-self: stretch;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  padding: var(--padding-sm) var(--padding-sm);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: var(--page-layout-width);
  margin: 0 auto;
}
.xterrangmailcomClickToCh {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-base);
  display: inline-block;
  font-style: italic;
  font-family: var(--font-inter);
  color: var(--color-gray-300);
  text-align: center;
}
.whitespace {
  padding-bottom: 126px;
}
.ghostButton {
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.lightText {
  font-family: var(--font-sans-serif);
  font-style: italic;
  font-weight: 400;
  font-size: var(--font-size-lg);
  line-height: var(--font-size-lg);
  /* identical to box height */
  text-align: center;
  color: var(--color-gray-300);
}
