.fanTicketsPoweredBy,
.lOOTBOX {
  position: relative;
  top: 0;
  left: 0;
  display: inline-block;
}
.lOOTBOX {
  font-size: var(--font-size-lg);
  font-family: var(--font-fira-sans);
}
.groupDiv {
  position: relative;
  flex-shrink: 0;
}
.frameDivUnverified {
  align-self: stretch;
  background-color: var(--color-yellow);
  display: flex;
  flex-direction: column;
  padding: var(--padding-4xs) var(--padding-xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.frameDiv {
  align-self: stretch;
  background-color: var(--color-teal);
  display: flex;
  flex-direction: column;
  padding: var(--padding-4xs) var(--padding-sm);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.b,
.epicBattleEventClickToEd {
  position: relative;
  font-size: var(--font-size-lg);
  display: inline-block;
  font-family: var(--font-inter);
  color: var(--color-black);
  text-align: center;
}
.epicBattleEventClickToEd {
  font-size: var(--font-size-base);
  color: var(--color-gray-500);
}
.frameButton,
.frameDiv1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.frameDivThumbs {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-lg) 0;
  box-sizing: border-box;
}
.frameDiv1small {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-sm) 0 0;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  .frameDivThumbs {
    padding: 0;
  }
}
.frameButton {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  flex-direction: row;
  gap: var(--gap-lg);
}
.frameDiv1 {
  align-self: stretch;
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-lg) 0;
  box-sizing: border-box;
}
.frameDiv1Padding {
  display: flex;
  align-self: stretch;
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-lg) var(--padding-md);
  box-sizing: border-box;
}
.lootboxPreviewContainer {
  display: flex;
  align-self: stretch;
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-lg) var(--padding-xs);
  box-sizing: border-box;
  gap: var(--gap-lg);
}
.scanForFanTickets {
  position: relative;
  display: inline-block;
}
.image1Icon {
  position: relative;
  width: 362px;
  height: 357px;
  flex-shrink: 0;
  object-fit: cover;
}
.frameDiv2,
.frameDiv4 {
  display: flex;
  box-sizing: border-box;
  align-items: center;
}
.frameDiv2 {
  align-self: stretch;
  flex-shrink: 0;
  flex-direction: column;
  padding: var(--padding-md) 0 0;
  justify-content: flex-start;
  gap: 11px;
  font-size: 32px;
  color: #676767;
}
.frameDiv4 {
  border-radius: var(--br-sm);
  background-color: #ccffcb;
  flex-direction: row;
  padding: var(--padding-4xs) var(--padding-2xs);
  justify-content: center;
}
.copyLink {
  position: relative;
  font-size: var(--font-size-xl);
  display: inline-block;
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: center;
}
.frameButton1,
.frameDiv3 {
  align-self: stretch;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.frameButton1 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-2xs) var(--padding-sm);
  background-color: var(--color-teal);
  border-radius: var(--br-md);
  flex-direction: row;
  margin-bottom: var(--padding-xs);
}
.frameDiv3 {
  background-color: var(--color-white);
  flex-direction: column;
  /* padding: var(--padding-2xl) var(--padding-lg); */
  gap: var(--gap-lg);
  font-size: var(--font-size-base);
  color: #5e5e5e;
}
.xterrangmailcomClickToCh {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-sm);
  display: inline-block;
  font-style: italic;
  font-family: var(--font-inter);
  color: var(--color-gray-400);
  text-align: center;
}
.eventViewScanQRCodeResp,
.frameDiv5 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frameDiv5 {
  align-self: stretch;
  background-color: var(--color-white);
  /* padding: 0 var(--padding-lg) var(--padding-2xl); */
  box-sizing: border-box;
  justify-content: center;
}
.eventViewScanQRCodeResp {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  justify-content: flex-start;
  gap: var(--gap-0);
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-white);
  font-family: var(--font-inter);
}
.floatingButtonContainer {
  align-self: stretch;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  padding: var(--padding-sm) var(--padding-sm);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: var(--page-layout-width);
  margin: 0 auto;
}
.whitespace {
  padding-bottom: 190px;
}
.warningText {
  color: var(--color-red);
  font-family: var(--font-sans-serif);
  font-weight: 500;
}
.ghostButton {
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.lightText {
  font-family: var(--font-sans-serif);
  font-style: italic;
  font-weight: 400;
  font-size: var(--font-size-lg);
  line-height: var(--font-size-lg);
  /* identical to box height */
  text-align: center;
  color: var(--color-gray-300);
}
.fanTicketsPoweredBy {
  font-family: var(--font-sans-serif);
  font-style: italic;
  font-weight: 400;
  font-size: var(--font-size-lg);
  /* line-height: var(--font-size-lg); */
  /* identical to box height */
  text-align: center;
  color: var(--color-white);
}
